import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { REFERENCE, WindowComponent, WindowComponentProps, withTooltip } from 'summer';
import { hasEmpresa } from '../../../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../../../dependencies/empresaMFPDependency';
import { hasGrupoEmpresaConfig } from '../../../../../../dependencies/grupoEmpresaConfigDependency';
import { getDefaultReportBasicValues } from '../../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../../models/dtos/ger/reportViewOptions';
import { Reducers } from '../../../../../../store/ducks';
import { GlobalState } from '../../../../../../store/ducks/global.duck';
import VinculoIS from '../../../../../components/mfp/vinculoIS/VinculoIS';
import ReportForm from '../../../../../components/report/ReportForm';
import { CLASS_KEY, ProvisaoFeriasRV, REPORT_EVENT, REPORT_TITLE } from './provisaoFeriasRV';

const DEFAULT_VALUES: ProvisaoFeriasRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  trabalhadores: [],
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, REFERENCE.value),
  exibirConfiguracaoDadosEmpresa: false,
  refInicialLabel: 'Referência',
  refInicialRequired: true,
};

const ProvisaoFeriasReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<ProvisaoFeriasRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  const [isDisabledNaoAfastados, setIsDisabledNaoAfastados] = useState(true);

  const [isCheckedNaoDemitidos, setIsCheckedNaoDemitidos] = useState(false);

  const [isCheckedNaoAfastados, setIsCheckedNaoAfastados] = useState(false);

  const [isDisabledTrabalhadores, setIsDisabledTrabalhadores] = useState(false);

  const tooltip = (message: string, componentId: string) => withTooltip({ message, placement: 'top-start' }, componentId);

  useEffect(
    () => {
      setTimeout(() => {
        if (globalReducer.globalParameter.empresa !== null && globalReducer.globalParameter.empresaMFP !== null) {
          const referenciaInicial = globalReducer.globalParameter.empresaMFP.referencia;
          formProps.setValue('referenciaInicial', referenciaInicial, true);
        }
      }, 200);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const onChangeSelecionarNaoDemitidos = () => {
    if (isCheckedNaoDemitidos) {
      setIsDisabledNaoAfastados(true);
      setIsCheckedNaoAfastados(false);
      setIsCheckedNaoDemitidos(false);
      setIsDisabledTrabalhadores(false);
    } else {
      setIsDisabledNaoAfastados(false);
      setIsCheckedNaoDemitidos(true);
      setIsDisabledTrabalhadores(true);
    }
  };

  const onChangeSelecionarNaoAfastados = () => {
    if (isCheckedNaoAfastados) {
      setIsCheckedNaoAfastados(false);
    } else {
      setIsCheckedNaoAfastados(true);
    }
  };

  return (
    <WindowComponent
      title="Provisão de Férias"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[
        () => hasEmpresa(globalReducer.globalParameter),
        () => hasEmpresaMFP(globalReducer.globalParameter),
        () => hasGrupoEmpresaConfig(globalReducer.globalParameter),
      ]}
      wikiId={1965}
      wikiTitle={'Relatório - Provisão de Férias'}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
      >
        <Row style={{ marginBottom: '10px' }}>
          <Col>
            <CustomInput
              type="switch"
              label="Exibir somente os trabalhadores ativos(sem rescisão)"
              id="idisSelecionarNaoDemitidos"
              name="isSelecionarNaoDemitidos"
              innerRef={formProps.register}
              checked={isCheckedNaoDemitidos}
              onChange={onChangeSelecionarNaoDemitidos}
            />
            {tooltip(
              'Este campo filtra os trabalhadores que possuem data de demissão anterior a referência de geração do relatório',
              'isSelecionarNaoDemitidos'
            )}
          </Col>
        </Row>
        <Row style={{ marginBottom: '10px' }}>
          <Col>
            <CustomInput
              type="switch"
              label="Não exibir trabalhadores com afastamentos permanentes ou com mais de 180 dias"
              id="idisSelecionarNaoAfastados"
              name="isSelecionarNaoAfastados"
              innerRef={formProps.register}
              disabled={isDisabledNaoAfastados}
              checked={isCheckedNaoAfastados}
              onChange={onChangeSelecionarNaoAfastados}
            />
            {tooltip(
              'Este campo filtra os trabalhadores com afastamentos permanentes ou com mais de 180 dias pelo INSS durante o período aquisitivo correspondente',
              'isSelecionarNaoAfastados'
            )}
          </Col>
        </Row>
        <VinculoIS
          id="trabalhadores"
          formProps={{ ...formProps, validation: { required: true } }}
          label="Trabalhadores(s)"
          placeholder="Selecione Trabalhadore(s)"
          isPrependLabel={true}
          inputSearchProps={{ selectProps: { isMulti: true } }}
          disabled={isDisabledTrabalhadores}
        />
      </ReportForm>
    </WindowComponent>
  );
};

export default ProvisaoFeriasReportView;
